<template>
  <a-layout id="homeLayout" :style="{ overflow: 'auto', height: '100vh' }">
    <the-heading></the-heading>
    <a-layout-content>
      <a-layout>
        <a-layout-sider class="leftSider" :style="{ width: '28vw', flex: '0 0 auto', 'max-width': '28vw', 'min-width': '28vw' }">
          <div class="currentVideo">
            <video class="videoA" src="" id="rtcA" autoplay></video>
            <video class="videoB" src="" id="rtcB" autoplay></video>
          </div>
          <div class="currentVideoInfo">
            <table class="currentVideoInfoTable">
              <tr><td>求助位置:</td><td>{{location}}</td></tr>
              <tr><td>设备编号:</td><td>{{machineId}}</td></tr>
              <tr><td>姓    名:</td><td>{{name}}</td></tr>
              <tr><td>性    别:</td><td>{{sex}}</td></tr>
              <tr><td>身份证号:</td><td>{{identity}}</td></tr>
            </table>
          </div>
        </a-layout-sider>
        <a-layout-content :style="{ width: '34vw'}">
          <div class="content">
            <h1 class="caption"><a-icon type="notification" /> 通话队列</h1>
            <a-table :columns="columns" :dataSource="data" :pagination="{ pageSize: 4 }" rowKey="id">
              <a slot="name" slot-scope="text">{{ text }}</a>
              <span slot="action" slot-scope="text, record">
                <a-button class="" @click="qh()" type="primary" size="small">通话</a-button>
                <a-button class="hidden" @click="hq()" type="danger" size="small">挂断</a-button>
              </span>
            </a-table>
            <a-dropdown>
              <a-menu slot="overlay" @click="handleMenuClick">
                <a-menu-item key="1">1st item</a-menu-item>
                <a-menu-item key="2">2nd item</a-menu-item>
                <a-menu-item key="3">3rd item</a-menu-item>
              </a-menu>
              <a-button class="launchPlan"> 启动预案 <a-icon type="down" /> </a-button>
            </a-dropdown>
            <a-divider class="adivider"/>
            <h1 class="caption"><a-icon type="file-protect" /> 事件记录</h1>
            <a-textarea placeholder="填写记录" :rows="4" />
            <a-button class="save">保 存</a-button>
            <a-divider class="adivider2"/>
            <div style="text-align: left;">
              <a-button type="primary" @click="showModal">消息派发</a-button>
              <a-button type="primary" @click="showCalling">快速上报</a-button>
            </div>
            <div>
              <a-modal title="消息派发" v-model="visible" @ok="handleOk" okText="确认" cancelText="取消">
                <a-transfer
                  :dataSource="mockData"
                  showSearch
                  :listStyle="{
                    width: '210px',
                    height: '300px',
                  }"
                  :locale="{searchPlaceholder: '请输入搜索内容',itemUnit: '项',itemsUnit: '项',notFoundContent: '暂无数据'}"
                  :targetKeys="targetKeys"
                  @change="handleChange"
                  :render="item => `${item.name}`"
                >
                  <a-button
                    size="small"
                    style="float:right;margin: 5px"
                    @click="getMock"
                    slot="footer"
                    slot-scope="props"
                  >
                    reload
                  </a-button>
                </a-transfer>
              </a-modal>
            </div>
            <div>
              <a-modal
                title="消息描述"
                :visible="msgVisible"
                :confirm-loading="confirmLoading"
                okText="发送"
                cancelText="取消"
                @ok="msgHandleOk"
                @cancel="handleCancel"
              >
                <p>{{ ModalText }}</p>
              </a-modal>
            </div>
          </div>
        </a-layout-content>
        <a-layout-sider :style="{ width: '37vw', flex: '0 0 auto', 'max-width': '37vw', 'min-width': '37vw' }">
          <ul class="videoContent">
            <li><div class="video2"></div></li>
            <li><div class="video2"></div></li>
            <li><div class="video2"></div></li>
            <li><div class="video2"></div></li>
            <li><div class="video2"></div></li>
            <li><div class="video2"></div></li>
            <li><div class="video2"></div></li>
            <li><div class="video2"></div></li>
          </ul>
        </a-layout-sider>
      </a-layout>
    </a-layout-content>
  </a-layout>
</template>

<script>
import TheHeading from '../components/TheHeading'
import { Layout, Button, Icon, Table, Dropdown, Menu, Input, Divider, Modal, Transfer } from 'ant-design-vue'
import axios from 'axios'
const columns = [
  {
    title: '队列',
    dataIndex: 'id',
    key: 'id',
    scopedSlots: { customRender: 'id' }
  },
  {
    title: '终端名称',
    dataIndex: 'machineNumber',
    key: 'machineNumber',
    ellipsis: true
  },
  {
    title: '状态',
    dataIndex: 'pause',
    key: 'pause',
    ellipsis: true
  },
  {
    title: '位置',
    dataIndex: 'machineZone',
    key: 'machineZone',
    ellipsis: true
  },
  {
    title: '',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]
export default {
  name: 'Home',
  components: {
    TheHeading,
    ALayout: Layout,
    ALayoutSider: Layout.Sider,
    ALayoutContent: Layout.Content,
    AButton: Button,
    AIcon: Icon,
    ATable: Table,
    ADropdown: Dropdown,
    AMenu: Menu,
    AMenuItem: Menu.Item,
    ATextarea: Input.TextArea,
    ADivider: Divider,
    AModal: Modal,
    ATransfer: Transfer
  },
  data () {
    return {
      location: '',
      machineId: '',
      name: '',
      sex: '',
      identity: '',
      data: [],
      columns,
      visible: false,
      mockData: [],
      targetKeys: [],
      account: window.sessionStorage.account || 'admin',
      isJoin: false,
      userList: [],
      roomid: 'webrtc_1v1', // 指定房间ID
      isCall: false, // 正在通话的对象
      isToPeer: false, // 是否建立了 P2P 连接
      peer: null,
      offerOption: {
        offerToReceiveAudio: 1,
        offerToReceiveVideo: 1
      },
      ModalText: '',
      msgVisible: false,
      confirmLoading: false
    }
  },
  methods: {
    qh () {
      let target = event.currentTarget
      target.setAttribute('class', 'hidden')
      let _el = event.currentTarget.nextElementSibling
      _el.setAttribute('class', 'ant-btn ant-btn-danger ant-btn-sm')
    },
    hq () {
      let target = event.currentTarget
      target.setAttribute('class', 'hidden')
      let _el = event.currentTarget.previousElementSibling
      _el.setAttribute('class', 'ant-btn ant-btn-primary ant-btn-sm')
    },
    getHomeInfo () {
      axios.get('../../static/mock/index.json')
        .then(this.getHomeInfoSucc)
    },
    getHomeInfoSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.location = data.location
        this.machineId = data.machineId
        this.name = data.name
        this.sex = data.sex
        this.identity = data.identity
        this.data = data.Ask_queue
        this.mockData = data.userList
        this.ModalText = data.event_handle.handleResult
      }
      console.log(res)
    },
    handleMenuClick (e) {
      console.log('click', e)
    },
    showModal () {
      this.visible = true
    },
    handleOk (e) {
      console.log(e)
      // this.visible = false
      this.msgVisible = true
    },
    getMock () {
      const targetKeys = []
      this.targetKeys = targetKeys
    },
    handleChange (targetKeys, direction, moveKeys) {
      console.log(targetKeys, direction, moveKeys)
      this.targetKeys = targetKeys
    },
    showCalling () {
      this.$router.push('/Calling')
      window.location.reload()
    },
    msgHandleOk (e) {
      // this.ModalText = 'The modal will be closed after two seconds'
      this.confirmLoading = true
      setTimeout(() => {
        this.visible = false
        this.msgVisible = false
        this.confirmLoading = false
      }, 1000)
    },
    handleCancel (e) {
      console.log('Clicked cancel button')
      this.msgVisible = false
    }
  },
  mounted () {
    this.getHomeInfo()
    this.getMock()
  }
}
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.ant-layout-content{
  background-color: #fff;
}
.leftSider{
  background-color: #fff;
}
.currentVideo{
  position: relative;
  width: 100%;
  // background: url("../assets/images/video1.png") no-repeat;
  background-size: 100% auto;
  video{
    background-color: #000;
  }
  .videoA{
    object-fit: fill;
    width: 100%;
    height: 300px;
  }
  .videoB{
    object-fit: fill;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 99;
    width: 100px;
    height: 150px;
  }
}
.currentVideoInfo{
  margin: 10px;
  .currentVideoInfoTable{
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    border: 1px solid #e8e8e8;
    border-radius: 5px;
    tr{
      border-bottom: 1px solid #e8e8e8;
      td{
        font-size: 14px;
        height: 32px;
        line-height: 32px;
        &:first-child{
          width: 100px;
          text-align: right;
        }
        &:last-child{
          text-align: left;
          padding-left: 10px;
        }
      }
    }
  }
}
.content{
  margin: 10px 10px 20px;
}
.launchPlan{
  position: relative;
  top: -50px;
}
.ant-divider{
  margin: 4px 0;
}
.save{
  margin-top: 6px;
  margin-bottom: 10px;
  float: right;
}
.caption{
  height: 26px;
}
.adivider{
  position: relative;
  top: -20px;
}
.adivider2{
  margin-bottom: 10px;
}
.videoContent{
  width: 100%;
  li{
    float: left;
    width: 50%;
    div{
      background: url("../assets/images/video2.png") no-repeat;
      background-size: 100% auto;
      padding-top: 68%;
      height: 0px;
    }
  }
}
.hidden{
  display: none;
}
</style>



