<template>
  <a-layout id="homeLayout" :style="{ overflow: 'auto', height: '100vh' }">
    <the-heading></the-heading>
    <a-layout-content>
      <iframe class="ifrm" ref="iframe" width="100%" height="100%" frameborder="0" scrolling="auto"></iframe>
      <div>
        <a-modal title="快速通话" v-model="visible" @ok="handleOk" okText="立即通话" cancelText="取消">
          <a-transfer
            :dataSource="mockData"
            showSearch
            :listStyle="{
              width: '210px',
              height: '300px',
            }"
            :locale="{searchPlaceholder: '请输入搜索内容',itemUnit: '项',itemsUnit: '项',notFoundContent: '暂无数据'}"
            :targetKeys="targetKeys"
            @change="handleChange"
            :render="item => `${item.name}`"
          >
            <a-button
              size="small"
              style="float:right;margin: 5px"
              @click="getMock"
              slot="footer"
              slot-scope="props"
            >
              reload
            </a-button>
          </a-transfer>
        </a-modal>
      </div>
    </a-layout-content>
    <a-layout-footer>
      <a-row class="icon">
        <a-col :span="7"></a-col>
        <a-col :span="2"><span class="iconfont">&#xe73b;</span><br/><label>语音通话</label></a-col>
        <a-col :span="2"><span class="iconfont">&#xe660;</span><br/><label>多人视频</label></a-col>
        <a-col :span="2"><span class="iconfont">&#xe67e;</span><br/><label>通讯录</label></a-col>
        <a-col :span="2"><span class="iconfont">&#xe637;</span><br/><label>发送图片</label></a-col>
        <a-col :span="2"><span class="iconfont">&#xe661;</span><br/><label>文字聊天</label></a-col>
        <a-col :span="7"></a-col>
      </a-row>
    </a-layout-footer>
  </a-layout>
</template>

<script>
import TheHeading from '../components/TheHeading'
import { Layout, Button, Row, Col, Modal, Transfer } from 'ant-design-vue'
import axios from 'axios'

export default {
  name: 'Calling',
  components: {
    TheHeading,
    ALayout: Layout,
    ALayoutSider: Layout.Sider,
    ALayoutContent: Layout.Content,
    ALayoutFooter: Layout.Footer,
    ARow: Row,
    ACol: Col,
    AButton: Button,
    AModal: Modal,
    ATransfer: Transfer
  },
  data () {
    return {
      data: [],
      visible: false,
      targetKeys: [],
      userList: [],
      mockData: [],
      locationHref: ''
      // config,
      // conferenceUI: {},
      // videosContainer,
      // btnSetupNewRoom,
      // roomsList
    }
  },
  methods: {
    getUrl () {
      this.$refs.iframe.contentWindow.location.replace('https://localhost:9528/static/index.html')
    },
    getHomeInfo () {
      axios.get('../../static/mock/index.json')
        .then(this.getHomeInfoSucc)
    },
    getHomeInfoSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.mockData = data.userList
      }
      console.log(res)
    },
    showModal (locationHref) {
      this.visible = true
      this.locationHref = locationHref
    },
    handleOk (e) {
      console.log(e)
      this.visible = false
      alert(this.locationHref)
    },
    getMock () {
      const targetKeys = []
      this.targetKeys = targetKeys
    },
    handleChange (targetKeys, direction, moveKeys) {
      console.log(targetKeys, direction, moveKeys)
      this.targetKeys = targetKeys
    }
  },
  mounted () {
    this.getUrl()
    this.getHomeInfo()
    this.getMock()
    window.showModal = this.showModal
  }
}
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.ant-layout-footer{
  text-align: center;
  .icon{
    span{
      margin:0 20px;
      font-size: 36px;
      cursor: pointer;
    }
    label{
      position: relative;
      top: 2px;
      font-size: 12px;
    }
  }
}
</style>



