import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import Video from '@/pages/Video'
import Calling from '@/pages/Calling'
import Resource from 'vue-resource'

Vue.use(Router)
Vue.use(Resource)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/video',
      name: 'Video',
      component: Video
    },
    {
      path: '/calling',
      name: 'Calling',
      component: Calling
    }
  ]
})



