<template>
  <a-layout-header>
    <a-row class="antRow" type="flex" justify="space-around" align="middle" :gutter="10">
      <a-col :span="2">
        <span class="nowTime"><a-icon type="clock-circle" />&nbsp;{{ nowTime }}</span>
      </a-col>
      <a-col :span="2"><button class="btn">行车计划</button></a-col>
      <a-col :span="2"><button class="btn">作业管理</button></a-col>
      <a-col :span="2"><button class="btn">乘客管理</button></a-col>
      <a-col :span="8" class="title2"><label>宁波地铁</label></a-col>
      <a-col :span="2"><button class="btn">综合监控</button></a-col>
      <a-col :span="2"><button class="btn">资产管理</button></a-col>
      <a-col :span="2"><button class="btn">应急管理</button></a-col>
      <a-col :span="2" class="weekAndDate" type="flex">
        <a-col :span="24">
          {{ nowWeek }}
        </a-col>
        <a-col :span="24">
          {{ nowDate }}
        </a-col>
      </a-col>
    </a-row>
  </a-layout-header>
</template>

<script>
import { Layout, Icon, Row, Col } from 'ant-design-vue'
export default {
  name: 'TheHeading',
  components: {
    ALayoutHeader: Layout.Header,
    AIcon: Icon,
    ARow: Row,
    ACol: Col
  },
  data () {
    return {
      nowDate: '',
      nowTime: '',
      nowWeek: ''
    }
  },
  methods: {
    currentTime () {
      setInterval(this.getDate, 500)
    },
    getDate () {
      var _this = this
      let yy = new Date().getFullYear()
      let mm = new Date().getMonth() + 1
      let dd = new Date().getDate()
      let week = new Date().getDay()
      let hh = new Date().getHours()
      let mf = new Date().getMinutes() < 10 ? '0' + new Date().getMinutes() : new Date().getMinutes()
      if (week === 1) {
        this.nowWeek = '星期一'
      } else if (week === 2) {
        this.nowWeek = '星期二'
      } else if (week === 3) {
        this.nowWeek = '星期三'
      } else if (week === 4) {
        this.nowWeek = '星期四'
      } else if (week === 5) {
        this.nowWeek = '星期五'
      } else if (week === 6) {
        this.nowWeek = '星期六'
      } else {
        this.nowWeek = '星期日'
      }
      _this.nowTime = hh + ' : ' + mf
      _this.nowDate = yy + '-' + mm + '-' + dd
    }
  },
  mounted () {
    this.currentTime()
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
.ant-layout-header{
  padding: 0 10px;
  color: #fff;
  background: #0d1633;
}
.btn{
  width: 86px;
  height: 34px;
  line-height: 28px;
  font-size: 16px;
  background-color: #0d3966;
  border-left: none;
  border-right: none;
  border-top: 1px solid #1878ab;
  border-bottom: 1px solid #1878ab;
  outline: none;
  cursor: pointer;
}
.title2{
  height: 64px;
  background: url("../assets/images/bg_title.png") no-repeat;
  background-size: 100% 100%;
  text-align: center;
  label{
    position: relative;
    top: -4px;
    font-size: 20px;
  }
}
.weekAndDate{
  font-size: 12px;
  line-height: 12px;
  white-space: nowrap;
}
.nowTime{
  white-space: nowrap;
}
</style>



